            "0,-66.66vh",
            "-33.33vh,-66.66vh",
            "-66.66vh,-66.66vh",
          ],
    };
  },
  computed: {
    dex() {
      return this.arr.indexOf(this.spa);
    },
  },
  methods: {
    changeIndex(arr, index1, index2) {
      arr[index1] = arr.splice(index2, 1, arr[index1])[0];
      return arr;
    },
  },
  created() {
    this.arr = this.arr.sort((x, y) => Math.random() - 0.5);
  },
